<template>
  <v-layout row wrap mt-3>
    <v-flex xs6 pa-2 lg5 offset-lg1>
      <v-card dark pa-2>
        <v-card-title>
          <v-icon left>fas fa-chart-line</v-icon>
          <span>ＩＮＦＯＲＭＡＴＩＯＮ</span>
        </v-card-title>
        <table id="info">
          <tr>
            <td>対象部材点数</td>
            <td>{{ cnt.all }}</td>
          </tr>
          <tr>
            <td>完了部材点数</td>
            <td>{{ cnt.fin }}</td>
          </tr>
          <tr>
            <td>在庫超過点数</td>
            <td>{{ cnt.over }}</td>
          </tr>
          <tr>
            <td>その他集計総額</td>
            <td>{{ cnt.inv_price }}</td>
          </tr>
        </table>
      </v-card>
    </v-flex>
    <v-flex xs12 mt-4 lg10 offset-lg1>
      <v-text-field name="search" label="ＳＥＡＲＣＨ" id="search" v-model="search"></v-text-field>
    </v-flex>
    <v-flex xs12 mt-2 lg10 offset-lg1>
      <v-card dark>
        <v-card-title primary-title>
          <v-icon left>fas fa-table</v-icon>
          <span>ＤＡＴＡ ＬＩＳＴ</span>
          <v-spacer></v-spacer>
          <v-btn @click="a4=!a4" small flat>
            <v-icon left>fas fa-table</v-icon>
            <span>GET PDF</span>
          </v-btn>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="items"
          class="data-list"
          pagination.sync="pagination"
          item-key="item_id"
          loading="true"
          :search="search"
          :rows-per-page-items="[20, 50,100,{'text':'All','value':-1}]"
          :expand="false"
        >
          <template v-slot:items="props">
            <tr @click="props.expanded = !props.expanded">
              <td>
                <v-progress-circular
                  :rotate="360"
                  :size="20"
                  :width="15"
                  :value="props.item.per"
                  color="teal"
                ></v-progress-circular>
              </td>
              <td>{{ props.item.item_code }}</td>
              <td>{{ props.item.item_name !== null ? props.item.item_name.slice(0, 8) : '' }}</td>
              <td>{{ props.item.item_model !== null ? props.item.item_model.slice(0, 14) : '' }}</td>
              <td>{{ props.item.num_inv }}</td>
              <td>{{ props.item.inv_price }}</td>
            </tr>
          </template>
          <template v-slot:expand="props">
            <v-card flat>
              <item-info :item_code="props.item.item_code" :item_rev="props.item.item_rev"></item-info>
            </v-card>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
    <v-dialog
      v-model="a4"
      v-if="a4"
      scrollable
      persistent
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="a4 = !a4">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Settings</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click="print__pdf('makepdf')">ＰＲＩＮＴ</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text class="a4-back">
          <EtcPdf></EtcPdf>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import itemInfo from "../../ItemData/ItemInfo";
import EtcPdf from "./EtcPdf";

export default {
  components: {
    itemInfo,
    EtcPdf
  },
  data: function() {
    return {
      headers: [
        { text: "率", value: "per", align: "center" },
        { text: "品目コード", value: "item_code", align: "center" },
        { text: "品名", value: "item_name", align: "center" },
        { text: "品目形式", value: "item_model", align: "center" },
        { text: "在庫集計数", value: "need_num", align: "center" },
        { text: "集計金額", value: "sum_inv", align: "center" }
      ],
      cnt: {
        fin: 0,
        all: 0,
        over: 0,
        inv_price: 0
      },
      items: [],
      search: null,
      a4: false,
      dataLoading: undefined
    };
  },
  created: async function() {
    await this.init();
    this.dataLoading = setInterval(() => {
      this.init();
    }, 10000);
  },
  methods: {
    async init() {
      await axios.get("/inventory/buzai-etc-list").then(res => {
        this.items = res.data;
        let cnt = this.cnt;
        cnt.fin = cnt.all = cnt.over = cnt.inv_price = 0;
        res.data.forEach(ar => {
          cnt.all = cnt.all + 1;
          cnt.inv_price = cnt.inv_price + ar.inv_price;
          if (ar.per === null || ar.per < 0) {
            ar.per = 0;
          } else if (ar.per === 100) {
            cnt.fin = cnt.fin + 1;
          } else if (ar.per > 100) {
            cnt.over = cnt.over + 1;
          }
        });
      });
    }
  },
  beforeDestroy: function() {
    clearInterval(this.dataLoading);
  }
};
</script>
